import React from 'react';
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'

// 自定义图片展示

export default function ImageZoom({ width = 96, src = "", alt="Image", square = false }) {

  return (
    <Zoom>
      {square ? <img alt={alt} src={src} width={width} /> : <img alt={alt} src={src} width={width} height={width} />}
    </Zoom>
  )
}
